
.cart
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cartItem
{
    width: 700px;
    height: 250px;
    display: flex;
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px;
}


.cartItem img
{
    width: 200px;
}

.cartItem .desc
{
    width: 100%;
    font-size: 30px;
}

.countHandler input
{
    width: 40px;
    text-align: center;
    font-weight: bolder;
}



.checkout button
{
    width: 150px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: #fff;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
}